
import React, { Component } from 'react';
import '../App.css';
import './sectionImage.scss'
import { Row, Col } from 'react-bootstrap';

class HomeAiSectionImage extends Component {

    constructor(props) {
        super(props)
        this.state = {
        }

    }

    render() {
        let hoverstate = this._HIDE
        console.log(this.props.onWhichSectionMouseFocused);
        
        if (this.props.shouldShow && this.props.onWhichSectionMouseFocused === this.props.sectionIndex + '') {
            hoverstate = 'ai-shrink'
        } else if (this.props.shouldShow && this.props.onWhichSectionMouseFocused !== this.props.sectionIndex + '') {
            hoverstate = 'ai-detach'
        } else if (!this.props.shouldShow && this.props.onWhichSectionMouseFocused === this.props.sectionIndex + '') {
            // console.warn('presented in other section')
        } else if (!this.props.shouldShow && this.props.onWhichSectionMouseFocused !== this.props.sectionIndex + '') {
            hoverstate = 'ai-merge'
        }
        let isChrome = !!window.chrome && (!!window.chrome.webstore || !!window.chrome.runtime);
        let videoForChrome = isChrome? ([<img alt={'ai screen front'} id={'ai-phone-front-outline'} src="/img/home/ai_phone_front_white.png" srcSet="/img/home/ai_phone_front_white.png 1x,/img/home/ai_phone_front_white@1.5x.png 1.5x,/img/home/ai_phone_front_white@2x.png 2x,/img/home/ai_phone_front_white@3x.png 3x" />,
        <video id={'ai-phone-front-content'} autoPlay loop>
        <source type='video/mp4' src='/video/ai-user-beefit-screen.mp4' media='(min-device-pixel-ratio:1), (-webkit-min-device-pixel-ratio:1), (min--moz-device-pixel-ratio:1), (-o-min-device-pixel-ratio:1)' />
    </video>]) : 
        (<img alt={'ai screen search'} id={'ai-phone-search-front'} src="/img/home/ai_phone_front.png" srcSet="/img/home/ai_phone_front.png 1x,/img/home/ai_phone_front@1.5x.png 1.5x,/img/home/ai_phone_front@2x.png 2x,/img/home/ai_phone_front@3x.png 3x" />)


        return (

            <Row className={'ai-image-row m-5 align-self-end'}>

                <Col className={'p-0'}>
                    <figure className={'d-none m-0 d-sm-flex figure-l ' + hoverstate + '-l'} key={'ai-section-image-1'}><img alt={'ai phone guide'} id={'ai-phone-guide'} src="/img/home/ai_screen_guide.png" srcSet="/img/home/ai_screen_guide.png 1x,/img/home/ai_screen_guide@1.5x.png 1.5x,/img/home/ai_screen_guide@2x.png 2x,/img/home/ai_screen_guide@3x.png 3x" /></figure>
                </Col>

                <Col className={'center p-0'}>

                    <figure className={'d-none m-0 position-absolute d-sm-flex'} key={'ai-section-image-2'}><img alt={'ai phone back'} id={'ai-phone-back'} src="/img/home/ai_phone_back.png" srcSet="/img/home/ai_phone_back.png 1x,/img/home/ai_phone_back@1.5x.png 1.5x,/img/home/ai_phone_back@2x.png 2x,/img/home/ai_phone_back@3x.png 3x" /></figure>
                    <figure className={'d-none m-0 position-absolute d-sm-flex ' + hoverstate + '-v'} id={'ai-phone-front-content-figure'} key={'ai-section-image-3'}>
                    {videoForChrome}
                        {/* <video>
                        <source type='video/mp4' src='video-high-res.mp4' media='(min-device-pixel-ratio:2), (-webkit-min-device-pixel-ratio:2), (min--moz-device-pixel-ratio:2), (-o-min-device-pixel-ratio:2)' />
                        <source type='video/webm' src='video-high-res.webm' media='(min-device-pixel-ratio:2), (-webkit-min-device-pixel-ratio:2), (min--moz-device-pixel-ratio:2), (-o-min-device-pixel-ratio:2)' />
                        <source type='video/mp4' src='video.mp4' media='(max-device-pixel-ratio:1), (-webkit-max-device-pixel-ratio:1), (max--moz-device-pixel-ratio:1), (-o-max-device-pixel-ratio:1)' />
                        <source type='video/webm' src='video.webm' media='(max-device-pixel-ratio:1), (-webkit-max-device-pixel-ratio:1), (max--moz-device-pixel-ratio:1), (-o-max-device-pixel-ratio:1)' />
                    </video> */}
                    </figure>

                </Col>
                <Col className={'p-0'}>
                    <figure className={'d-none m-0 d-sm-flex figure-r ' + hoverstate + '-r'} key={'ai-section-image-4'}><img alt={'ai screen search'} id={'ai-phone-search'} src="/img/home/ai_screen_search.png" srcSet="/img/home/ai_screen_search.png 1x,/img/home/ai_screen_search@1.5x.png 1.5x,/img/home/ai_screen_search@2x.png 2x,/img/home/ai_screen_search@3x.png 3x" /></figure>
                </Col>

            </Row>

        )
    }
}

export default HomeAiSectionImage;