import React, { Component } from 'react';
import '../App.css';
import './About.scss';
import BurgerMenu from '../widget/BurgerMenu';
import FadeInText from '../FadeInText';
import '../GlobalAnimation.scss';
import { Tween, Timeline } from 'react-gsap';
import { TweenLite } from 'gsap/all';
import SVG from 'svg.js';
import "svg.filter.js";

// import 'svg.filter.js';


class About extends Component {

    constructor(props) {
        super(props)
        this.state = {
            animationStep: 1
        }
    }

    overCount = 0

    isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

    componentDidMount = () => {
        this.createProfileSvg()
        this.createHrLine()
    }

    createHrLine = () => {
        let svg = SVG.get('horizontalLine')
        let line = svg.rect(0, 1).move(10, 29).fill('white')
        let light = svg.rect(0, 1).move(10, 29).fill('#f3b79b')
        let lightShadow = svg.rect(0, 1.6).move(10, 29).fill('#f3b79b')
        lightShadow.filter(element => {
            element.attr({ filterUnits: 'userSpaceOnUse', primitiveUnits: 'userSpaceOnUse' })
            element.gaussianBlur(9)
        })
        lightShadow.animate({ duration: 1000, delay: 7600, ease: '<' }).attr({ x: 10, width: 270 })
        lightShadow.animate({ duration: 1000, delay: 0, ease: '>' }).attr({ x: 540, width: 0 })
        light.animate({ duration: 1000, delay: 7600, ease: '<' }).attr({ x: 10, width: 270 })
        light.animate({ duration: 1000, delay: 0, ease: '>' }).attr({ x: 540, width: 0 })
        line.animate({ duration: 1000, delay: 7600, ease: '<' }).attr({ x: 10, width: 270 })
        line.animate({ duration: 1000, delay: 0, ease: '>' }).attr({ x: 10, width: 540 })
    }

    createProfileSvg = () => {
        let maskShape = SVG.get('mask').get(0)
        let svg = SVG.get('profileSvg')
        let feDisplacementMap = SVG.get('filter').get(1)

        maskShape.animate({ duration: 2000, ease: '<>', delay: 9200 }).attr('r', 154).after(() => {
            maskShape.animate({ duration: 2000, ease: '<>' }).attr('r', 120).loop(100, true)
        })
        svg.mouseover(() => {
            feDisplacementMap.stop()
            maskShape.stop()
            feDisplacementMap.animate({ duration: 50, ease: '<>' }).attr('scale', feDisplacementMap.attr('scale') + 30).animate({ duration: 150, ease: '<>' }).attr('scale', 10).after(() => {
                // feDisplacementMap.stop()
                // feDisplacementMap.animate({ duration: 5000, ease: '>' }).attr('scale', 0)
            })
            maskShape.animate({ duration: 50, ease: '<>' }).attr('r', maskShape.attr('r') + 20).animate({ duration: 150, ease: '<>' }).attr('r', 90).after(() => {
                // maskShape.animate({ duration: 5000, ease: '>' }).attr('r', 83)
            })
        })
        svg.mouseout(() => {
            feDisplacementMap.stop()
            maskShape.stop()
            feDisplacementMap.animate({ duration: 2000, ease: '<>' }).attr('scale', 40)
            maskShape.animate({ duration: 2000, ease: '<>' }).attr('r', 154).after(() => {
                maskShape.animate({ duration: 2000, ease: '<>' }).attr('r', 120).loop(100, true)
            })
        })

    }

    render() {
        const profileSvg = this.isSafari ?
            (
                <svg
                    version="1.1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="368px" height="379px" viewBox="0 0 368 379" style={{ 'enableBackground': 'new 0 0 358 369' }} xmlSpace="preserve">
                    <defs>
                        <filter id="filter">
                            <feTurbulence type="fractalNoise" baseFrequency="0.05" id="feTurbulence"
                                numOctaves="3" result="fractalNoise" >
                            </feTurbulence>
                            <feDisplacementMap in2="turbulence" in="SourceGraphic"
                                scale='40' id="disp" xChannelSelector="R" yChannelSelector="G" >
                            </feDisplacementMap>

                            <animate
                                xlinkHref={'#feDisplacementMap'}
                                id="anim-dialiate"
                                attributeName="scale"
                                from="40"
                                to="0"
                                begin="8s"
                                dur="3s"
                            />
                        </filter>
                        <mask id="mask" width="358" height="369" >
                            <circle cx="184" cy="184" r="154" fill="#ffffff" filter="url(#filter)" id="circle">
                            </circle>
                        </mask>
                    </defs>
                    <image x="0" y="0" height="358" width="369" href="https://i.imgur.com/czxzNem.jpg"></image>
                </svg>
            ) : (
                <svg id='profileSvg'
                    version="1.1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="368px" height="379px" viewBox="0 0 368 379" style={{ 'enableBackground': 'new 0 0 358 369' }} xmlSpace="preserve">
                    <defs>
                        <filter id="filter">
                            <feTurbulence type="fractalNoise" baseFrequency="0.05" id="feTurbulence"
                                numOctaves="3" result="fractalNoise" >
                            </feTurbulence>
                            <feDisplacementMap in2="turbulence" in="SourceGraphic"
                                scale='40' xChannelSelector="R" yChannelSelector="G" >
                            </feDisplacementMap>
                        </filter>
                        <mask id="mask" width="358" height="369" >
                            <circle cx="184" cy="184" r="0" fill="#ffffff" filter="url(#filter)" id="circle">
                            </circle>
                        </mask>
                    </defs>
                    <image x="0" y="0" height="358" width="369" href="https://i.imgur.com/czxzNem.jpg" mask="url(#mask)" ></image>
                </svg>
            )

        const horizontalLine = (
            <svg id="horizontalLine"
                version="1.1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink"
                x="0px" y="0px" width="270px" height="30px" viewBox="0 0 540 60" style={{ 'enableBackground': 'new 0 0 540 60' }} xmlSpace="preserve">
            </svg>
        )
        return (
            <section className={'about-section hero-section d-flex'}>
                <div className={'row d-flex flex-row col-md-9 mx-auto d-flex align-content-center align-items-center'}>
                    <article className={'col-md-4'}>
                        <h1 className={'h1 cn-light'}>
                            <FadeInText innerText={'蘇桓晨'} animationType={'fadeInBottom'} animationDelay={0} id={'display-name'}></FadeInText>
                        </h1>
                        <h1 className="h1 cn-light">
                            <FadeInText innerText={'Huan-Chen Su'} animationType={'fadeInBottom'} animationDelay={0} id={'display-name'}></FadeInText>
                        </h1>
                        <p className="cn">
                            <FadeInText innerText={'UI/UX設計師。我相信UI、程式和UX都是為了解決使用者遇到的問題而共同發揮作用的技能，'} animationType={'fadeInBottom'} animationDelay={900} childClass={'about-me-text'}></FadeInText><br />
                            <FadeInText innerText={'希望看到世界因為我的設計而變得更美好。'} animationType={'fadeInBottom'} animationDelay={2500} childClass={'about-me-text'}></FadeInText><br />
                            <FadeInText innerText={'目前正在尋找重視UI/UX設計的工作團隊。'} animationType={'fadeInBottom'} animationDelay={3300} childClass={'about-me-text'}></FadeInText><br />
                        </p>
                    </article>
                    {horizontalLine}
                    <figure className={'col-md-4'}>
                        {profileSvg}
                        <div id="svg-container"></div>
                    </figure>
                </div>
            </section>

        )
    }

}

export default About;
