import React, { Component } from 'react';
import './App.css';
import './fullPager.scss';
import SVG from 'svg.js';
import { Route } from 'react-router-dom'
import FadeInText from './FadeInText';
import FadeInLink from './FadeInLink';
import ColumnLineBackground from './ColumnLineBackground';
import HomeProjectSection from './HomeProjectSection';
import HomeFooterSection from './HomeFooterSection';
import FSectionImage from './homeSectionImageControllers/HomeFSectionImage';
import AiSectionImage from './homeSectionImageControllers/HomeAiSectionImage';
import CoeSectionImage from './homeSectionImageControllers/HomeCoeSectionImage';
