    applymMouseSensor = (yHeight, triggeringSvg,...respondingSvg) => {
        
        triggeringSvg.mouseover(()=>{
            triggeringSvg.stop()
            respondingSvg.forEach((svg)=>{
                svg.stop()
            })
            triggeringSvg.animate({ ease: '<>' }).transform({a:1, b:0, c:0, d:1, e:0, f:yHeight})
            respondingSvg.forEach((svg)=>{
                svg.animate({ ease: '<>' }).transform({a:1, b:0, c:0, d:1, e:0, f:yHeight})
            })
            console.log('in');
        })
        triggeringSvg.mouseout(()=>{
            triggeringSvg.stop()
            respondingSvg.forEach((svg)=>{
                svg.stop()
            })
            triggeringSvg.animate({ duration: 350, ease: '<>' }).transform({a:1, b:0, c:0, d:1, e:0, f:0})
            respondingSvg.forEach((svg)=>{
                svg.animate({ duration: 350, ease: '<>' }).transform({a:1, b:0, c:0, d:1, e:0, f:0})
            })
            console.log('out');
        })
    }

    applymMouseSensor2 = (...svg) => {
        
        svg.forEach( (eachSvg, index ,svgArray) => {
            eachSvg.mouseover(()=>{
                svgArray.forEach(v => v.stop())
                svgArray.forEach( v => v.animate({ ease: '<>' }).transform({a:1, b:0, c:0, d:1, e:0, f:-50}) )
                console.log('in');
            })
        })
        
        svg.forEach( (eachSvg, index ,svgArray) => {
            eachSvg.mouseout(()=>{
                svgArray.forEach(v => v.stop())
                svgArray.forEach( v => v.animate({ ease: '<>' }).transform({a:1, b:0, c:0, d:1, e:0, f:0}) )
                console.log('out');
            })
        })
    }


