        let uiXFrame = SVG(parentId).size(uiXFrameWidthPercent + 'vw', uiXFrameWidthPercent / uiXFrameRatio + 'vw').viewbox({ x: 0, y: 0, width: uiXFrameWidth, height: uiXFrameHeight }).attr({ 'id': id, 'class': 'xFrame' });

