        let uiXFowardSlash = uiXFrame.path('M24.4,1.2L24.1,0L11.9,3.3l0.3,1.2c1.5-0.3,2.9-0.6,3.6-0.5c1.1,0.2,1.5,1,1.8,1.8c0.2,0.9,0.2,1.9,0.2,2.8l-0.1,84c-0.1,2.5-1,3.3-1.9,4c-1,0.8-2.1,1.3-3.3,1.7l0.3,1.2l12.2-3.3l-0.3-1.2c-1.2,0.3-2.5,0.4-3.8,0.3c-0.8,0-1.3-0.9-1.6-1.8c0-0.2-0.1-0.3-0.1-0.5l0-2.6l0.1-83.9C19.2,3.3,21.8,1.9,24.4,1.2z').fill('#F8F8FB');

        uiXBackSlash.clipWith(xBackSlashClipShape);
