import React, { Component } from "react";
import "../App.css";
import { Col, Row } from "react-bootstrap";

class HomeCoeSectionImage extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  imageFocusing = () => {};

  imageHovered = () =>
    this.props.onWhichSectionMouseFocused === this.props.sectionIndex + "";

  render() {
    let hoverstate = this._HIDE;
    console.log("coe", this.props.shouldShow);
    console.log(this.props.onWhichSectionMouseFocused, this.imageHovered());

    let scrollControl = "";

    if (this.props.shouldShow && this.imageHovered()) {
      scrollControl = " scroll-whole";
    } else if (this.props.shouldShow && !this.imageHovered()) {
      scrollControl = " scroll-part";
    } else {
      scrollControl = " scroll-none";
    }

    return [
      <figure
        className={"pl-0 pr-2 d-none d-sm-flex position-absolute"}
        key={"coe-section-image-1"}
      >
        <div>
          <img
            alt={"macbook outline"}
            id={"macbook-outline"}
            src="/img/home/coe-macbook.png"
            srcSet="/img/home/coe-macbook.png 1x,/img/home/coe-macbook@1.5x.png 1.5x,/img/home/coe-macbook@2x.png 2x,/img/home/coe-macbook@3x.png 3x"
          />
          <div
            className={
              "coe-cover-page-container position-absolute" + scrollControl
            }
          ></div>
        </div>
      </figure>
    ];
  }
}

export default HomeCoeSectionImage;
