import React, { Component } from 'react';
import './App.css';
import SectionButton from './SectionButton'
import './ColumnLineBackground.scss';
import { Col } from 'react-bootstrap';
import { Link } from 'react-router-dom'
import './HomeProjectSection.scss'

class HomeProjectSection extends Component {

    constructor(props) {
        super(props)
        this.state = {
            isNowShow: this.props.isShowing
        }
    }

    static getDerivedStateFromProps(props, state) {
        let nextNowShow = props.isShowing
        if (nextNowShow === true && state.isNowShow === false) {
            console.log(props.sectionIndex, 'presenting');
        } else if (nextNowShow === false && state.isNowShow === true) {
            console.log(props.sectionIndex, 'leaving');
        }
        return { isNowShow: props.isShowing }
    }

    isJustShow = this.props.isShowing

    handleSectionMouseEnter = () => {
        this.setState({ isHovering: true }, () => {
            console.log(this.state.isHovering);
            this.props.reportIsFocusd(this.props.sectionIndex)
        })
    }

    handleSectionMouseLeave = () => {
        this.setState({ isHovering: false }, () => {
            console.log(this.state.isHovering);
            this.props.reportIsFocusd(-1)
        })
    }

    render() {

        let moveFromTopCSS = this.props.paralloxEffectControl()
        // let moveFromTopCSS = ''
        let sectionName = this.props.sectionName()
        let fontColor = ' ' + sectionName + '-primary-color '
        let sectionColor = ' ' + sectionName + '-section-color '
        let sectionBgColor = ' ' + sectionName + '-section-bg-color '
        let fontBgColor = ' ' + sectionName + '-primary-bg-color'

        return (
            <Col sm={7} className={'row justify-content-end section-card-area col-sm-11 col-md-9 col-lg-7 ' + moveFromTopCSS}>
                <div className={'row align-self-center m-0'} >
                    <Col className={'align-self-end p-0 '} ><h2 className={'project-number d-sm-flex d-none en-xblod p-0 float-right' + sectionColor} >{this.props.sectionText.projectNumber}</h2></Col>
                    <Col xs={11} onMouseLeave={() => { this.handleSectionMouseLeave() }} onMouseEnter={() => { this.handleSectionMouseEnter() }} className={'section-card rounded pl-3 pr-0' + sectionBgColor} >
                        <Link to="/Feather" className={'row section-card-link d-flex'}>
                            <Col xs={6} sm={4} className={'article text-area align-self-center d-flex flex-column pl-sm-5 pr-0 py-0'} >
                                <div className={'mb-auto d-none d-xl-block'} >
                                    <h6 className={'project-type en-xblod mv-0' + fontColor} >{this.props.sectionText.projectType}</h6>
                                    <h5 className={'project-year en-light' + fontColor} >{this.props.sectionText.projectYear}</h5>
                                </div>
                                <div className={'my-auto'} >
                                    <h1 id={'project-title'} className={'en-bold my-0' + fontColor} >{this.props.sectionText.projectEnTitle}</h1>
                                    <h3 className={'project-title cn-light my-0 mb-3' + fontColor} >{this.props.sectionText.projectCnTitle}</h3>
                                    <div className={'py-1 my-2 col-1 d-sm-block d-none' + fontBgColor}></div>
                                    <p className={'project-intro cn' + fontColor} >{this.props.sectionText.projectIntro}</p>
                                </div>
                                <div className={'mt-auto mb-4 d-none d-xl-block'} >
                                    <h6 className={'project-role-title en-xblod ' + fontColor} >{this.props.sectionText.projectRoleTitle}</h6>
                                    <h5 className={'project-role en-light ' + fontColor} >{this.props.sectionText.projectRole}</h5>
                                </div>
                            </Col>
                            <Col className={'img-area ml-auto mr-0 p-0 row d-flex justify-content-center align-items-center'} >
                                {this.props.sectionImage}
                            </Col>
                            <Col xs={2} sm={1} className={'section-btn-container p-0'} >
                                <SectionButton sectionIndex={this.props.sectionIndex} sectionName={sectionName} shouldButtonPresent={this.state.isHovering} sectionContent={() => this.props.sectionContent} deltaBtnColor={this.props.deltaBtnColor}></SectionButton>
                            </Col>
                        </Link>
                    </Col>
                </div>
            </Col>
        )
    }
}

export default HomeProjectSection;